<template>
  <Upload
    ref="upload"
    :show-upload-list="false"
    accept="image/*"
    :on-format-error="uploadError"
    :on-success="uploadSuccess"
    type="drag"
    :action="action"
    class="upload"
  >
    <div class="upload-box">
      <Icon type="ios-camera" size="30" v-if="!modelValue"></Icon>
      <img :src="modelValue" >
    </div>
  </Upload>
</template>

<script>
import { Upload } from "view-ui-plus";
export default {
  name: "viUploadSingleImg",
  components: { Upload },
  props: {
    modelValue: {
      type: String,
      default: ""
    }
  },
  model: {
    prop: "modelValue",
    event: "change",
  },
  computed: {
    action() {
      if (process.env.NODE_ENV === "development") {
        return "/api/upload/singleImage";
      } else {
        return "/upload/singleImage";
      }
    }
  },
  methods: {
    uploadError(err) {
      this.$Message.error(`图片上传失败：${err.message || err}`);
    },
    uploadSuccess(res) {
      this.$Message.success("上传成功");
      this.$emit("update:modelValue", res.data);
    },
  }
};
</script>

<style lang="less" scoped>
.upload {
  width: 140px;
  height: 140px;
  display: inline-block;
  .upload-box {
    width: 140px;
    height: 140px;
    line-height: 140px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
